const app = getApp();
import pageBottom from '@/components/page-bottom';
import uniLogin from '@/components/uni-login';
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
	export default {
		components: {
		    uniLogin,pageBottom,uniLoadMore
		},
		data() {
			return {
				titleNViewBackground: '',
				swiperCurrent: 0,
				swiperLength: 0,
				carouselList: [],
				cateList: [],
				goodsList: [],
				searchVal:'',
				cateId:0
			};
		},
		onShow() {
			let sid = 1;
		},
		onLoad() {
			const session_key = uni.getStorageSync('session_key')
			if(!session_key){
				this.showLogin = 1
			}else{
				this.showLogin = 0
			}
			this.loadData("refresh");
			this.loadCateList();
		},
		onPageScroll(e) {
		    //兼容iOS端下拉时顶部漂移
		    if (e.scrollTop >= 0) {
		        this.headerPosition = "fixed";
		    } else {
		        this.headerPosition = "absolute";
		    }
		},
		//下拉刷新
		onPullDownRefresh() {
		    this.loadData("refresh");
		},
		//加载更多
		onReachBottom() {
		    this.loadData('more');
		},
		methods: {
			//加载分类
			async loadCateList() {
			    const res = await app.request(
			        this.$Url + "/clmsj/Goodshtml/getgoodstypelist"
			    );
			    this.cateList = res.data.data || [];
			    this.cateList.unshift({id: 0, name:'全部'})
			    this.cateId = this.cateList[0].id;
			},
			//加载商品 ，带下拉刷新和上滑加载
			async loadData(type = "add", loading) {
			    //没有更多直接返回
			    if (type === "add") {
			        if (this.loadingType === "nomore") {
			            return;
			        }
			        this.loadingType = "loading";
			    } else {
			        this.loadingType = "more";
			    }
			
			    if (type === "refresh") {
			        this.page = 1;
			    }
			
			    uni.request({
			        url: this.$Url + "/clmsj/Goodshtml/get_shop_goodslist",
			        header: {  auth: uni.getStorageSync("session_key") },
			        data: {
			            page: this.page,
						keyword: this.searchVal,
						catid : this.cateId
			        },
			        method: "POST",
			        success: res => {
			            if (res.data.code == 0) {
			                let goodsList = res.data.data;
			                this.page++;
			                if (type == "refresh") {
			                    this.goodsList = goodsList;
			                } else {
			                    this.goodsList = this.goodsList.concat(goodsList);
			                }
							console.log('DDD')
							console.log(this.goodsList.length)
			                //判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
			                this.loadingType = goodsList.length < 10 ? "nomore" : "more";
			                if (type === "refresh") {
			                    if (loading == 1) {
			                        uni.hideLoading();
			                    } else {
			                        uni.stopPullDownRefresh();
			                    }
			                }
			            }
			        }
			    });
			},
			searchGoods(){
				this.loadData();
			},
			switchCate(cate_id) {
			    this.cateId = cate_id;
			    this.loadData("refresh");
			},
			//轮播图切换修改背景色
			swiperChange(e) {
				const index = e.detail.current;
				this.swiperCurrent = index;
				this.titleNViewBackground = this.carouselList[index].background;
			},
			//详情页
			navToDetailPage(item) {
				uni.navigateTo({
				    url: "/pages/product/product?goods_id=" + item.id + "&sid=1"
				});
			}
		}
	};
